.nav {
  @include e(heading) {
    color: $color-txt-secondary;
    padding: 1rem;
    text-align: center;

    @include mq($breakpoint-xx-lg) {
      padding: 2rem;
    }

    @include mq($breakpoint-xxx-lg) {
      text-align: left;
    }
  }

  @include e(break) {
    @include mq($breakpoint-x-lg) {
      display: none;
    }
  }

  @include e(container__link) {
    color: $color-txt-secondary;
    font-weight: $font-weight-bold;

    @include mq($breakpoint-xxx-lg) {
      font-size: $font-size-xx-lg;
    }
  }
}

.hero {
  @include e(heading) {
    margin-bottom: 1.5rem;
    padding-top: 1rem;
    text-align: center;

    @include mq($breakpoint-sm) {
      font-size: $font-size-xxx-lg;
    }

    @include mq($breakpoint-lg){
      padding-top: 1.5rem;
    }

    @include mq($breakpoint-x-lg) {
      font-size: $font-size-grande;
      text-align: left;
    }

    @include mq($breakpoint-xxx-lg) {
      font-size: $font-size-x-grande;
    }
  }

  @include e(break) {
    @include mq($breakpoint-x-lg) {
      display: none;
    }
  }

  @include e(text) {
    display: none;

    @include mq($breakpoint-x-lg) {
      display: inline;
      margin-bottom: 1.5rem;
      width: 50%;
    }
  }

  @include e(bttn__link) {
    color: $color-txt-secondary;
    font-weight: $font-weight-bold;
  }
}

.services {
  @include e(heading) {
    margin-bottom: 1.5rem;
    text-align: center;

    @include mq($breakpoint-x-lg) {
      font-size: $font-size-x-lg;
    }

    @include mq($breakpoint-xxx-lg) {
      font-size: $font-size-xx-lg;
      margin-bottom: 3rem;
      width: 100%;
    }
  }

  @include e(card__heading) {
    margin-bottom: .5rem;
  }

  @include e(card__text) {
    text-align: center;
  }
}

.contact {
  @include e(heading) {
    margin-bottom: .5rem;
    text-align: center;

    @include mq($breakpoint-x-lg) {
      font-size: $font-size-x-lg;
    }

    @include mq($breakpoint-xxx-lg) {
      font-size: $font-size-xx-lg;
    }
  }

  @include e(text) {
    margin-bottom: 1.5rem;
    text-align: center;
    width: 288px;

    @include mq($breakpoint-xxx-lg) {
      margin-bottom: 3rem;
    }
  }
}

.footer {
  @include e(text) {
    color: $color-txt-secondary;
    font-size: $font-size-x-sm;
    text-align: center;
  }

  @include e(link) {
    color: $color-link-footer;
  }
}
