/* BEM */

@mixin e($element) {
  &__#{$element} {
    @content;
  }
}

@mixin m($modifier) {
  &--#{$modifier} {
    @content;
  }
}

@mixin p-e($pseudo-e) {
  &::#{$pseudo-e} {
    @content;
  }
}

@mixin p-c($pseudo-c) {
  &:#{$pseudo-c} {
    @content;
  }
}

/* FLEXBOX */

@mixin flexbox($flex-direction, $justify-content) {
  display: flex;
  flex-direction: $flex-direction;
  justify-content: $justify-content;
}

/* @MEDIA */

@mixin mq($breakpoint: $md-brkpoint) {
  @media (min-width: $breakpoint) {
    @content;
  }
}
