/* COLORS */

// base colors
$color-white: #fff;
$color-black: #000;

// theme colors
$color-navyblue: #232943;
$color-darkblue: #191f39;
$color-lightpurple: #f3f6ff;
$color-darkgrey: #3C424E;
$color-cyan: #00b7eb;

// color usage
$color-bg-primary: $color-darkblue; // primary background color
$color-bg-secondary: $color-lightpurple; // secondary background color
$color-bg-tertiary: $color-navyblue; // tertiary background color

$color-txt-primary: $color-darkgrey; // primary text color
$color-txt-secondary: $color-white; // secondary text color

$color-link-footer: $color-cyan; // color of the footer links

/* FONTS */

// font family
$font-family-primary: 'Montserrat', 'Trebuchet MS', Helvetica, sans-serif;

// font sizes
$font-size-x-sm: 12px;
$font-size-sm: 14px;
$font-size-base: 16px;
$font-size-lg: 18px;
$font-size-x-lg: 20px;
$font-size-xx-lg: 24px;
$font-size-xxx-lg: 30px;
$font-size-grande: 36px;
$font-size-x-grande: 42px;

// font weights
$font-weight-base: 400;
$font-weight-bold: 600;
$font-weight-x-bold: 700;

/* BREAKPOINTS */

$breakpoint-sm: 321px;
$breakpoint-md: 425px;
$breakpoint-lg: 481px;
$breakpoint-x-lg: 660px;
$breakpoint-xx-lg: 769px;
$breakpoint-xxx-lg: 1024px;
$breakpoint-grande: 1200px;
