.contact {
  @include e(form) {
    @include mq($breakpoint-x-lg) {
      width: 400px;
    }

    @include mq($breakpoint-grande) {
      width: 600px;
    }

    @include e(input) {
      border: none;
      border-radius: 5px;
      box-sizing: border-box;
      font-size: $font-size-sm;
      margin-bottom: 1rem;
      padding: 1rem;
      width: 100%;
    }

    @include e(textarea) {
      height: 175px;
    }

    @include e(bttn) {
      background-color: $color-bg-primary;
      border: none;
      border-radius: 5px;
      color: $color-txt-secondary;
      font-weight: $font-weight-bold;
      padding: .75rem 0;
      width: 100%;
    }
  }
}
