body {
  color: $color-txt-primary;
  font-family: $font-family-primary;
  font-size: $font-size-base;
}

a {
  color: $color-txt-primary;
  line-height: normal;
  text-decoration: none;
}

h1 {
  font-size: $font-size-xxx-lg;
}

h2 {
  font-size: $font-size-xx-lg;
}

h3 {
  font-size: $font-size-lg;
}

h4 {
  font-size: $font-size-base;
}

p {
  font-size: $font-size-sm;
}

h1, h2, h3, h4, p {
  margin: 0;
}

textarea {
  resize: none;
}
