// layout (grid, padding, margins) for all container elements;
// excludes form and image (png, jpg, svg) elements;
// includes the nav heading element due to it's positioning
// underneath the phone number banner on mobile views;

.header {
  @include mq($breakpoint-grande) {
    @include flexbox(row, center);
    background-color: $color-bg-primary;
  }
}

.nav {
  // grid layout for the navigation on mobile views
  display: grid;
  grid-template-rows: auto;
  grid-template-areas: 'container'
                       'heading';

  background-color: $color-bg-primary;
  position: fixed;
  width: 100%;
  z-index: 1000;

  @include mq($breakpoint-xxx-lg) {
    grid-template-columns: auto;
    grid-template-areas: 'heading container'
                         'heading container';

    position: static;
  }

  @include mq($breakpoint-grande) {
    width: 1200px;
  }

  @include e(heading) {
    grid-area: heading;
  }

  @include e(container) {
    grid-area: container;

    @include flexbox(row, center);
    align-items: center;
    background-color: $color-bg-tertiary;
    padding: .75rem 1rem;

    @include mq($breakpoint-xxx-lg) {
      background-color: $color-bg-primary;
      justify-content: flex-end;
      padding: 2rem;
    }
  }
}

.hero {

  @include mq($breakpoint-grande) {
    @include flexbox(row, center);
    padding: 4rem 0 0;
  }

  @include e(container) {
    @include m(invisible) {
      height: 144px;

      @include mq($breakpoint-x-lg) {
        height: 110px;
      }

      @include mq($breakpoint-xx-lg) {
        // display: none;
      }

      @include mq($breakpoint-xxx-lg) {
        display: none;
      }
    }

    @include m(visible) {
      background-image: url(../img/hero-img-base.jpg);
      background-position: bottom;
      background-repeat: no-repeat;
      height: 416px;
      padding: 0 1rem;

      @include mq($breakpoint-sm) {
        background-image: url(../img/hero-img-sm.jpg);
        height: 450px;
      }

      @include mq($breakpoint-lg) {
        background-image: url(../img/hero-img-lg.jpg);
        background-position: bottom;
      }

      @include mq($breakpoint-x-lg) {
        @include flexbox(column, center);
        align-items: flex-start;
        background-image: url(../img/hero-img-x-lg.jpg);
        background-position: top;
        height: 350px;
        padding: 0 4rem;
      }

      @include mq($breakpoint-xx-lg) {
        background-image: url(../img/hero-img-xx-lg.jpg);
        background-position: top;
        background-size: cover;
        height: 400px;
      }

      @include mq($breakpoint-xxx-lg) {
        height: 500px;
      }

      @include mq($breakpoint-grande) {
        box-sizing: border-box;
        width: 1072px;
      }
    }

    @include m(content) {
      @include flexbox(column, center);
      align-items: center;

      @include mq($breakpoint-x-lg) {
        align-items: flex-start;
      }
    }
  }
}

.services {
  padding: 3rem 1rem;

  @include mq($breakpoint-md) {
    padding: 3rem 2rem;
  }

  @include mq($breakpoint-lg) {
    padding: 3rem;
  }

  @include mq($breakpoint-x-lg) {
    @include flexbox(column, center);
    align-items: center;
  }

  @include mq($breakpoint-xxx-lg) {
    @include flexbox(row, space-between);
    flex-wrap: wrap;
    padding: 5rem 4rem;
  }

  @include mq($breakpoint-grande) {
    box-sizing: border-box;
    margin: 0 auto;
    padding: 5rem 0;
    width: 1072px;
  }

  @include e(card) {
    @include flexbox(column, center);
    align-items: center;
    background-color: $color-bg-secondary;
    border-radius: 5px;
    box-sizing: border-box;
    padding: 2rem 1rem;

    &:nth-last-child(n+2) {
      margin-bottom: 1rem;
    }

    @include mq($breakpoint-x-lg) {
      padding: 2rem;
      width: 400px;
    }

    @include mq($breakpoint-xxx-lg) {
      width: 30%;

      &:nth-last-child(n+2) {
        margin: 0;
      }
    }
  }
}

.contact {
  @include flexbox(column, center);
  align-items: center;
  background-color: $color-bg-secondary;
  padding: 3rem 1rem;

  @include mq($breakpoint-md) {
    padding: 3rem 2rem;
  }

  @include mq($breakpoint-lg) {
    padding: 3rem;
  }

  @include mq($breakpoint-xxx-lg) {
    padding: 5rem 3rem;
  }
}

.footer {
  background-color: $color-bg-primary;
  padding: 2rem 1rem 1rem;

  @include mq($breakpoint-xxx-lg) {
    padding: 4rem 0 2rem;
  }
}
